import React, { useEffect, useState } from 'react';
import ModuloBase from '../components/ModuloBase';
import Toolbar from '../components/Toolbar';
import Toast from '../components/Toast';
import TableEmpty from '../components/TableEmpty';
import { listCategories } from '../services/categories';
import { listSubcategories, createSubcategory, updateSubcategory, deleteSubcategory } from '../services/subcategories';
import FloatingInput from '../components/FloatingInput';

export default function Subcategorias() {
  const [categories, setCategories] = useState([]);
  const [categoryId, setCategoryId] = useState('');
  const [rows, setRows] = useState([]);
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(1);
  const [limit] = useState(10);
  const [loading, setLoading] = useState(false);
  const [toast, setToast] = useState({ show: false, variant: 'success', message: '' });
  const [showModal, setShowModal] = useState(false);
  const [editing, setEditing] = useState(null);
  const [name, setName] = useState('');

  useEffect(() => {
    (async () => {
      try {
        const resp = await listCategories({ page: 1, limit: 200 });
        const data = Array.isArray(resp?.data) ? resp.data : (resp || []);
        setCategories(data);
        if (data.length) setCategoryId(String(data[0].id));
      } catch { /* ignore */ }
    })();
  }, []);

  const load = async () => {
    if (!categoryId) { setRows([]); setTotal(0); return; }
    try {
      setLoading(true);
      const resp = await listSubcategories(categoryId, { page, limit });
      const data = Array.isArray(resp?.data) ? resp.data : (resp?.rows || resp || []);
      setRows(data);
      setTotal(resp?.total ?? data.length);
    } catch (err) {
      setToast({ show: true, variant: 'danger', message: err.message || 'No se pudo cargar' });
    } finally { setLoading(false); }
  };
  useEffect(() => { load(); /* eslint-disable-next-line */ }, [page, categoryId]);

  const openNew = () => { setEditing(null); setName(''); setShowModal(true); };
  const openEdit = (r) => { setEditing(r); setName(r.name || ''); setShowModal(true); };
  const onSubmit = async (e) => {
    e.preventDefault();
    try {
      if (editing) await updateSubcategory(editing.id, { name }); else await createSubcategory({ category_id: categoryId, name });
      setToast({ show: true, variant: 'success', message: editing ? 'Subcategoría actualizada' : 'Subcategoría creada' });
      setShowModal(false);
      load();
    } catch (err) {
      setToast({ show: true, variant: 'danger', message: err.message || 'Error al guardar' });
    }
  };
  const onDelete = async (r) => {
    if (!window.confirm('¿Eliminar subcategoría?')) return;
    try { await deleteSubcategory(r.id); setToast({ show: true, variant: 'success', message: 'Eliminado' }); load(); }
    catch (err) { setToast({ show: true, variant: 'danger', message: err.message || 'Error al eliminar' }); }
  };

  const totalPages = Math.max(1, Math.ceil((total || 0) / limit));

  return (
    <ModuloBase titulo="Gestión de Subcategorías" descripcion="Aquí podrás ver, crear, editar y eliminar subcategorías asociadas a una categoría.">
      <Toolbar
        left={
          <div className="row g-2 w-100">
            <div className="col-sm-4">
              <label className="form-label">Categoría</label>
              <select className="form-select" value={categoryId} onChange={e=>{ setCategoryId(e.target.value); setPage(1); }}>
                {categories.map(c => <option key={c.id} value={c.id}>{c.name}</option>)}
              </select>
            </div>
          </div>
        }
        right={<button className="btn btn-primary" onClick={openNew} disabled={!categoryId}>+ Agregar subcategoría</button>}
      />

      <div className="table-responsive mt-3">
        <table className="table table-striped align-middle">
          <thead>
            <tr>
              <th>ID</th>
              <th>Nombre</th>
              <th style={{width:140}}>Acciones</th>
            </tr>
          </thead>
          <tbody>
            {(rows || []).map(r => (
              <tr key={r.id}>
                <td>{r.id}</td>
                <td>{r.name}</td>
                <td className="d-flex gap-2">
                  <button className="btn btn-sm btn-outline-secondary" onClick={()=>openEdit(r)}>Editar</button>
                  <button className="btn btn-sm btn-outline-danger" onClick={()=>onDelete(r)}>Eliminar</button>
                </td>
              </tr>
            ))}
            {(!rows || rows.length === 0) && !loading && <TableEmpty colSpan={3} label="Sin subcategorías" />}
          </tbody>
        </table>
      </div>

      <div className="d-flex justify-content-between align-items-center">
        <div className="text-muted small">Total: {total}</div>
        <div className="btn-group">
          <button className="btn btn-outline-secondary" disabled={page<=1} onClick={()=>setPage(p=>Math.max(1,p-1))}>Anterior</button>
          <span className="btn btn-outline-secondary disabled">{page}/{totalPages}</span>
          <button className="btn btn-outline-secondary" disabled={page>=totalPages} onClick={()=>setPage(p=>Math.min(totalPages,p+1))}>Siguiente</button>
        </div>
      </div>

      {showModal && (
        <div className="modal fade show" style={{ display: 'block' }}>
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">{editing ? 'Editar' : 'Nueva'} subcategoría</h5>
                <button type="button" className="btn-close" onClick={()=>setShowModal(false)}></button>
              </div>
              <form onSubmit={onSubmit}>
                <div className="modal-body">
                  <FloatingInput id="subcat-name" label="Nombre" value={name} onChange={e=>setName(e.target.value)} required />
                </div>
                <div className="modal-footer">
                  <button type="button" className="btn btn-outline-secondary" onClick={()=>setShowModal(false)}>Cancelar</button>
                  <button type="submit" className="btn btn-primary" disabled={loading}>{loading ? 'Guardando...' : 'Guardar'}</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}

      <Toast show={toast.show} variant={toast.variant} onClose={()=>setToast({ ...toast, show: false })}>
        {toast.message}
      </Toast>
    </ModuloBase>
  );
}
